import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {IsmTeachingsPage} from "./ism-teachings.page";

const routes: Routes = [
    {
        path: '',
        component: IsmTeachingsPage,
        children: [
            {
                path: 'bible',
                children: [
                    {
                        path: '',
                        loadChildren: () => import('../ism-bible/ism-bible.module').then(m => m.IsmBiblePageModule)
                    }
                ]
            },
            {
                path: 'topic',
                children: [
                    {
                        path: '',
                        loadChildren: () => import('../ism-teachings-videos/ism-teachings-videos.module').then(m => m.IsmTeachingsVideosPageModule)
                    }
                ]
            }
        ]
    },
    {
        path: '',
        redirectTo: 'ism-teachings/bible',
        pathMatch: 'full'
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class IsmTeachingsPageRoutingModule {
}
